$color_1: #002329;
$color_2: #e100ff;
$done: rgb(225, 0, 255);
$primary-color: #29180e;
$primary-color-1: #29180e60;
$primary-color-2: #29180eb6;
  

@keyframes righttoleft{
    0%   {width:0; height: 0;}
    100% {width: 100%; height: 100%;}
  }

@mixin sliding-effect{
    animation: righttoleft 1s;
  }
  
@mixin sliding-effect10s{
    animation: righttoleft 6s;
}
  
@mixin flex {
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
}
  
body{
    @include flex();
    min-width: 100vw;
    min-height: 100vh;
    background-color: rgba(7, 16, 36, 0.883);
}





  